import React, { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import Navbar from "./Navbar";
import {
  Divider,
  Button,
  Box,
  Grid,
  GridItem,
  Text,
  Heading,
  Input,
} from "@chakra-ui/react";

import { Line } from "react-chartjs-2";
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend,
} from "chart.js";

// Registrasi komponen yang diperlukan di Chart.js
ChartJS.register(
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend
);

const endpoint = process.env.REACT_APP_API_URL;

const calculate_distance = (lat_from, lng_from, lat_to, lng_to) => {
  // Konversi derajat ke radian
  const toRad = (value) => (value * Math.PI) / 180;
  const R = 6371000; // Radius Bumi dalam meter
  const dLat = toRad(lat_to - lat_from); // Perbaikan urutan parameter
  const dLon = toRad(lng_to - lng_from); // Perbaikan urutan parameter
  const a =
    Math.sin(dLat / 2) * Math.sin(dLat / 2) +
    Math.cos(toRad(lat_from)) *
      Math.cos(toRad(lat_to)) *
      Math.sin(dLon / 2) *
      Math.sin(dLon / 2);

  const c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1 - a));

  const distance = R * c; // Jarak dalam meter
  return distance.toFixed(1);
};

const DataChart = () => {
  const navigate = useNavigate();

  const [data, setData] = useState([]);
  const [center_lat, setCenter_lat] = useState(-6.599584066525912);
  const [center_lon, setCenter_lon] = useState(106.8109289531242);

  // const names = data.map(item => item.name);
  // console.log(names); // Output: ['Alice', 'Bob', 'Charlie']

  useEffect(() => {
    const login = localStorage.getItem("isLogin");
    const admin = localStorage.getItem("isAdmin");
    if (login && admin) {
      fetch(`${endpoint}/datas`)
        .then((response) => response.json())
        .then((data) => {
          data.sort((a, b) => a.id - b.id);
          setData(data);
        })
        .catch((error) => {
          console.error("Error fetching parking slot data:", error);
        });
    } else {
      navigate("/login");
    }
  }, [navigate]);

  const dataJarak = {
    labels: data.map((item) => item.timestamp),
    datasets: [
      {
        label: "Jarak ke Home",
        data: data.map((item) =>
          calculate_distance(item.gps_lat, item.gps_lon, center_lat, center_lon)
        ),
        borderColor: "rgba(75,192,192,1)",
        backgroundColor: "rgba(75,192,192,0.2)",
        tension: 0.1,
      },
    ],
  };

  const dataGetaran = {
    labels: data.map((item) => item.timestamp),
    datasets: [
      {
        label: "Guncangan (m/s^2)",
        data: data.map((item) => item.getaran),
        borderColor: "rgba(153,102,255,1)",
        backgroundColor: "rgba(153,102,255,0.2)",
        tension: 0.1,
      },
    ],
  };

  // Opsi untuk chart
  const optionsJarak = {
    maintainAspectRatio: false,
    responsive: true,
    plugins: {
      legend: {
        display: false,
        position: "top",
      },
      title: {
        display: true,
        text: "Jarak ke Home",
      },
    },
    scales: {
      y: {
        beginAtZero: true,
      },
    },
  };

  // Opsi untuk chart
  const optionsGuncangan = {
    maintainAspectRatio: false,
    responsive: true,
    plugins: {
      legend: {
        display: false,
        position: "top",
      },
      title: {
        display: true,
        text: "Guncangan",
      },
    },
    scales: {
      y: {
        beginAtZero: true,
      },
    },
  };

  return (
    <>
      <Navbar />
      <Grid templateRows="auto" gap={10} p={0}>
        <GridItem>
          <Box height="400px" color="white">
            <Line data={dataJarak} options={optionsJarak} />
          </Box>
        </GridItem>

        <Divider borderWidth="3px" borderColor="gray.200" />

        <GridItem>
          <Box height="400px" color="white">
            <Line data={dataGetaran} options={optionsGuncangan} />
          </Box>
        </GridItem>
      </Grid>
    </>
  );
};

export default DataChart;
